 .article__comment {

     .box-heading {
         border-top: 1px solid rgba(0, 0, 0, 0.50);
         padding-top: 24px;
         margin-bottom: 20px;

         .title {
             color: var(--gray-900, #212529);
             font-family: Cormorant;
             font-size: 30px;
             font-weight: 600;
             line-height: 130%;
             text-transform: uppercase;
         }
     }

     .box-content {
         display: grid;
         gap: 20px;
     }

     .first-comment {
         display: grid;
         grid-template-columns: 1fr;
         gap: 16px;

         .avatar {
             width: 48px;
             height: 48px;
             border-radius: 50%;
             display: none;
         }

         .wrapper {
             display: grid;
             gap: 16px;

             //1280
             @include media-breakpoint-up(xl) {
                 align-items: center;
                 grid-template-columns: 1fr 150px;
             }

             textarea {

                 //1280
                 @include media-breakpoint-up(xl) {
                     grid-column: 1/3;
                 }
             }

             .note {
                 color: #888;
                 
                 font-size: 13px;
                 font-style: italic;
                 font-weight: 400;
             }
         }

         .wrapper-login {
             display: flex;
             align-items: center;
             gap: 6px;

             .text {
                 color: #6C757D;
                 font-family: var(--font-family-primary);
                 line-height: 20px;
             }

             .login {
                 color: #032B82;
                 font-family: var(--font-family-primary);
                 line-height: 20px;
             }

             .fb,
             .zalo,
             .google,
             .mail {
                 width: 26px;
                 height: 26px;
                 border-radius: 50%;
                 display: flex;
                 align-items: center;
                 justify-content: center;
             }

             [class*="ic-"] {
                 &:before {
                     font-size: 18px;
                     color: var(--white);
                 }
             }

             .zalo {
                 background: #03A5FA;
             }

             .google {
                 background: #cc4034;
             }

             .mail,
             .fb {
                 background: #3B5998;
             }
         }

         .btn-submit {
             margin-top: 0;
         }
     }

     .arrangement {
         display: flex;
         justify-content: space-between;
         align-items: center;
         padding: 0px 20px;
         background: #F7F7F7;
         min-height: 50px;

         .total {
             color: var(--gray-800, #343A40);
             
             font-size: 13px;
             font-weight: 500;
             text-transform: uppercase;
         }

         label {
             color: #888;
             
             font-size: 14px;
             font-weight: 400;
         }

         select {
             width: 100px;
             height: 32px;
             padding: 0px 10px;
             background: var(--white);
             color: var(--gray-600, #6C757D);
             
             font-size: 14px;
             font-weight: 400;
             border: none;
             margin-left: 10px;

             // 1024
             @include media-breakpoint-up(lg) {
                 width: 200px;
             }

         }
     }

     textarea {
         background: #F8F9FA;
         box-sizing: border-box;
         display: block;
         width: 100%;
         padding: 16px;
         border: none;

         &::placeholder {
             font-family: var(--font-family-primary);
             font-size: 16px;
             line-height: 21px;
             color: #6C757D;
             font-style: italic;
         }
     }

     .btn-submit {
         height: 40px;
         padding: 0px 20px;
         border-radius: 6px;
         background: var(--primary);
         float: right;
         color: var(--white);
         text-align: center;
         font-family: var(--font-family-primary);
         font-size: 14px;
         font-weight: var(--font-weight-semibold);
         text-transform: uppercase;
         margin-top: 16px;
     }

     .user-info {
         margin-left: auto;
         background: rgba(238, 238, 238, .5);
         border: 1px solid #eee;
         padding: 7px;
         display: none;
         align-items: center;
         margin-right: 10px;
         border-radius: 0;
         flex-direction: row;

         .avatar {
             width: 24px;
             height: 24px;
             position: relative;
             display: block;
             background: url(../img/no-ava.png) 0 0/24px no-repeat;
             border-radius: 50%;

             img {
                 position: absolute;
                 width: 100%;
                 height: 100%;
                 border-radius: 50%;
             }
         }

         .avatar+.name {
             margin-left: 8px;
             font-size: 14px;
         }

     }

     .filter-comment {
         display: flex;
         align-items: center;
         justify-content: space-between;
         padding: 8px 20px;
         background: var(--gray-200);
         margin-top: 30px;
         position: relative;

         .total-comment {
             font-family: var(--font-family-primary);
             font-weight: var(--font-weight-medium);
             font-size: 13px;
             line-height: 20px;
             text-transform: uppercase;
             color: var(--gray-800);
         }

         .wrap-select {
             position: relative;

             label {
                 font-family: var(--font-family-primary);
                 font-size: 13px;
                 line-height: 20px;
                 color: var(--gray-800);
                 margin-bottom: 0;
             }

             select {
                 font-family: var(--font-family-primary);
                 background: var(--white) url(../img/arrow.png?021123) center right 10px/18px no-repeat;
                 box-sizing: border-box;
                 outline: none;
                 padding: 10px;
                 font-weight: normal;
                 font-size: 13px;
                 line-height: 20px;
                 color: #555555;
                 cursor: pointer;
                 width: 200px;
                 appearance: none;
                 -moz-appearance: none;
                 -webkit-appearance: none;
                 position: relative;
                 border: none;
                 margin-left: 5px;
             }

             &:after {
                 position: absolute;
                 content: '';
                 right: 10px;
                 top: 16px;
             }
         }
     }

     .list-comment {
         margin-top: 32px;

         .avatar {
             width: 50px;
             min-width: 50px;
             height: 50px;
             border-radius: 50%;
             padding: 15px;
             background: #e5e5e5 url(../img/user.png?021123) center no-repeat;
             margin-top: 0;
             position: relative;
             display: none;

             img {
                 width: 100%;
                 height: 100%;
                 border-radius: 50%;
                 position: absolute;
                 top: 0;
                 right: 0;
             }
         }

         .content {
             width: 100%;

             .wrapper {
                 display: flex;
                 gap: 26px;
                 margin-bottom: 11px;
             }

             .name {
                 color: var(--gray-800, #343A40);
                 
                 font-size: 12px;
                 font-weight: 700;
                 line-height: 150%;
                 text-transform: uppercase;
             }

             .time {
                 position: relative;
                 color: #888;
                 
                 font-size: 13px;
                 font-weight: 400;
                 line-height: 150%;
                 text-transform: uppercase;

                 &::before {
                     position: absolute;
                     content: "";
                     width: 4px;
                     height: 4px;
                     background: rgba(0, 0, 0, 0.30);
                     top: 7px;
                     left: -14px;
                     border-radius: 50%;
                 }
             }

             .text {
                 color: var(--gray-800, #343A40);
                 
                 font-size: 16px;
                 font-weight: 400;
                 line-height: 150%;
             }

             .action {
                 margin-top: 8px;

                 .like {
                     font-weight: var(--font-weight-medium);
                     font-size: 14px;
                     line-height: 20px;
                     color: #6C757D;

                     &::before {
                         font-family: "icomoon";
                         content: "\e909";
                         position: relative;
                         font-size: 19px;
                         margin-right: 4px;
                         top: 3px;
                     }

                     &.liked {
                         color: var(--primary);
                     }
                 }

                 .count {
                     margin-left: 3px;
                 }

                 .reply {
                     font-weight: var(--font-weight-medium);
                     font-size: 14px;
                     line-height: 20px;
                     color: #6C757D;
                     margin-left: 44px;

                     &::before {
                         font-family: "icomoon";
                         content: "\e96d";
                         position: relative;
                         font-size: 19px;
                         margin-right: 4px;
                         top: 3px;
                     }
                 }
             }

             .wrap-reply {
                 margin-top: 8px;

                 .note-btn {
                     display: flex;
                     justify-content: flex-end;
                     align-items: center;
                     margin-top: 1.2rem;
                 }
             }
         }

         .btn-submit {
             text-transform: none;
             font-weight: var(--font-weight-normal);
         }
     }

     .wrap-outer {
         padding-top: 36px;
         margin-top: 16px;
         border-top: 1px solid var(--border-color);
     }

     .rank-1 {
         display: grid;
         grid-template-columns: 1fr;
         gap: 16px;
         border-top: 1px solid var(--gray-300);
         padding-top: 28px;
         margin-top: 16px;

         &:first-child {
             border-top: 0;
             padding-top: 0;
             margin-top: 0;
         }
     }

     .rank-2 {
         display: grid;
         grid-template-columns: 1fr;
         gap: 16px;
         margin-left: 66px;
         border-top: 1px solid var(--gray-300);
         padding-top: 20px;
         margin-top: 16px;

         .wrap-comment {
             margin-top: 15px;
         }

         .w-100.mt-3 {
             justify-content: flex-end;
         }

         .comment {
             display: flex;
         }

         .avatar {
             width: 30px;
             min-width: 30px;
             height: 30px;
         }
     }

     .show-more {
         font-weight: var(--font-weight-bold);
         position: relative;
         width: 240px;
         height: 40px;
         margin-left: auto;
         margin-right: auto;
         display: block;
         color: var(--primary);
         font-size: 16px;

         &::after {
             display: inline-block;
             width: 0;
             height: 0;
             margin-left: .2125em;
             vertical-align: .2125em;
             content: "";
             border-top: .25em solid;
             border-right: .25em solid transparent;
             border-bottom: 0;
             border-left: .25em solid transparent;
         }
     }
 }